body {
    background-color: gainsboro;
    margin: 0;
    padding-bottom: 80px;
    font-family: Roboto;
    font-weight: lighter;
}

h1 {
    font-weight: lighter;
}

.headerContainer {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    margin-bottom: 30px;
    width: 100%;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    height: 100%;
}

.userInfo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 200px;
    font-size: 24px;
}

.userInfo a {
    text-decoration: none;
    color: black;
}

.userInfo .btn-login {
    padding: 7px;
    font-weight: 400;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    color: white;
    border: none;
    border-radius: 5px;
    background-color: #4f78a2;
    cursor: pointer;
    text-decoration: none;
}